.containerNeon{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20%;
    
}

.containerNeon a {
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    border: 2px solid #0f0;
    text-transform: uppercase;
    font-family: Montserrat;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: 2px;
    color: white;
    -webkit-box-reflect: below 0px linear-gradient(transparent, #0002);
    transition: .5s;
    transition-delay: 0s;
}

.containerNeon a:hover{
    transition-delay: 1.2s;
    color: #000;
    box-shadow: 
    0 0 10px #0f0,
    0 0 20px #0f0,
    0 0 40px #0f0,
    0 0 80px #0f0,
    0 0 160px #0f0;
}

.containerNeon a span{
    position: relative;
    z-index: 100;
}

.containerNeon a:nth-child(2){
    -webkit-filter: hue-rotate(80deg);
            filter: hue-rotate(80deg);
}

.containerNeon a::before{
    content: '';
    position: absolute;
    left: -20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 2px;
    background: #0f0;
    box-shadow: 5px -8px 0 #0f0,
                5px 8px 0 #0f0;
    transition: width .5s, left .5s, height .5s, box-shadow 0.5s;
    transition-delay: 1s, 0.5s, 0s, 0s;
}

.containerNeon a:hover::before{
    width: 60%;
    height: 100%;
    left: -2px;
    box-shadow: 5px 0 0 #0f0,
                5px 0 0 #0f0;
    transition-delay: 0s, 0s, 0.5s, 1s;
    
}

.containerNeon a::after{
    content: '';
    position: absolute;
    right: -20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 2px;
    background: #0f0;
    box-shadow: -5px -8px 0 #0f0,
                -5px 8px 0 #0f0;
    transition: width .5s, left .5s, height .5s, box-shadow 0.5s;
    transition-delay: 1s, 0.5s, 0s, 0s;
}

.containerNeon a:hover::after{
    width: 60%;
    height: 100%;
    right: -2px;
    box-shadow: -5px 0 0 #0f0,
                -5px 0 0 #0f0;
    transition-delay: 0s, 0s, 0.5s, 1s;
    
}
